ft: 0;\n  display: inline-block;\n  position: absolute;\n  border: 2px solid $icon-color;\n  border-top: unset;\n  border-left: unset;\n  transition: transform 0.2s, right 0.2s, bottom 0.2s;\n  margin: 12px 6px;\n}\n\n%truncateLeftParent {\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n%truncateLeftChild {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  unicode-bidi: isolate;\n  direction: ltr;\n}\n\n%headerCellPadding {\n  padding: 0.1em 0;\n}\n\n%baseInnerCell {\n  display: flex;\n  flex-flow: row nowrap;\n  padding: 0 $cell-padding;\n  align-items: center;\n  width: 100%;\n  height: 100%;\n  &:hover {\n    & .copyButton {\n      display: block;\n    }\n  }\n}\n\n%cellBorderLeft {\n  content: '';\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  width: 1px;\n  height: 100%;\n  top: 0;\n  background-color: transparent;\n}\n\n%headerCellBorderBottom {\n  content: '';\n  position: absolute;\n  bottom: 0;\n  top: auto;\n  left: 1px;\n  width: 100%;\n  height: 1px;\n  display: block;\n  background-color: $header-border-color;\n}\n\n.hidden {\n  visibility: hidden;\n}\n\n.iconMenu {\n  position: absolute;\n  left: 0;\n  bottom: 0;\n  padding-bottom: 3.5px;\n\n  ul[role='menu'] {\n    background-color: $header-bg-color;\n    padding-left: 2px;\n    margin: 0px 0px 0px 4px;\n    border: none;\n\n    button {\n      width: 13px;\n      height: 11px;\n      svg {\n        fill: currentColor;\n        transform: scale(0.7);\n      }\n    }\n  }\n}\n\n.moveToRight {\n  left: auto;\n  right: 0;\n\n  ul[role='menu'] {\n    margin: 4px 0px 0px 0px;\n  }\n}\n\n// Concrete selectors\n\n@keyframes spin {\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n.bullet {\n  visibility: visible;\n  align-items: center;\n  background: $checkbox-background;\n  border-radius: $bullet-size;\n  border: calc(var(--border-width) * 1px) solid $checkbox-border;\n  color: $icon-color;\n  cursor: pointer;\n  display: flex;\n  height: $bullet-size;\n  justify-content: center;\n  outlin